.open-box-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vw 2vw;
  color: #f0f0f0;
  min-height: 100vh;
  background: #121212;
}

.box-container {
  position: relative;
  width: 100%;
  max-width: 400px;
  height: 300px;
  margin: 200px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plate,
.lid,
.card-reveal {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.plate {
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.lid {
  top: 90px;
  width: 80%;
  transition: transform 1s ease;
  z-index: 3;
}

.lid.open {
  animation: flyAway 1.4s forwards;
}

@keyframes flyAway {
  0% {
    transform: translateX(-50%) translateY(0) rotate(0);
  }
  40% {
    transform: translateX(-60%) translateY(-150px) rotate(-25deg);
  }
  100% {
    transform: translateX(-150%) translateY(-600px) rotate(-75deg);
  }
}

.card-reveal {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%) scale(0.1);
  transform-origin: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: transform 0.8s ease, opacity 0.8s ease;
  z-index: 2;
}

.card-reveal.visible {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}

.card-reveal img {
  height: auto;
  max-width: 100%;
  object-fit: contain;
  display: block;
}

.result-text {
  text-align: center;
  margin-top: 20px;
  color: #ff9f00;
}

.rarity-glow {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  filter: blur(60px);
  z-index: 0;
  opacity: 0.5;
  pointer-events: none;
}

.rarity-common {
  background: radial-gradient(circle, #aaaaaa 0%, #444444 80%);
}

.rarity-rare {
  background: radial-gradient(circle, #4287f5 0%, #0d1b3f 80%);
}

.rarity-epic {
  background: radial-gradient(circle, #9b30ff 0%, #2e003e 80%);
}

.rarity-legendary {
  background: radial-gradient(circle, #ff9f00 0%, #3f2200 80%);
}
