/* –ó–∞–ø—Ä–µ—Ç –≤—ã–¥–µ–ª–µ–Ω–∏—è —Ç–µ–∫—Å—Ç–∞ –ø–æ –≤—Å–µ–º—É –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—é */
body,
#root,
.safe-container {
  user-select: none;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE/Edge */
}

/* –ó–∞–ø—Ä–µ—Ç –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è –∏ –≤—ã–¥–µ–ª–µ–Ω–∏—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */
img {
  user-select: none;
  -webkit-user-drag: none;
  pointer-events: auto; /* –ß—Ç–æ–±—ã –∫–ª–∏–∫–∏ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫–∏ –æ—Å—Ç–∞–≤–∞–ª–∏—Å—å –∞–∫—Ç–∏–≤–Ω—ã–º–∏, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
}

.game-version {
  position: fixed;
  bottom: 1px;
  left: 1px;
  font-size: 10px;
  color: rgba(125, 125, 125); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π –±–µ–ª—ã–π —Ü–≤–µ—Ç */
  z-index: 10000; /* –ß—Ç–æ–±—ã –Ω–∞–¥–ø–∏—Å—å –±—ã–ª–∞ –ø–æ–≤–µ—Ä—Ö –¥—Ä—É–≥–∏—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
}
/* –ó–∞—Ç–µ–º–Ω—ë–Ω–Ω—ã–π —Ñ–æ–Ω */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #f0f0f0;
  overflow-x: hidden;
}
.safe-container {
  min-height: var(--tg-viewport-stable-height, 100vh);
  padding-top: var(--tg-safe-area-inset-top, env(safe-area-inset-top));
  padding-bottom: var(--tg-safe-area-inset-bottom, env(safe-area-inset-bottom));
  padding-left: var(--tg-safe-area-inset-left, env(safe-area-inset-left));
  padding-right: var(--tg-safe-area-inset-right, env(safe-area-inset-right));
  display: flex;
  flex-direction: column;
}
/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ñ–æ–Ω–æ–≤–æ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
.background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url("/public/photo_2025-07-29_06-44-47.jpg"); /* –∑–∞–º–µ–Ω–∏—Ç–µ –Ω–∞ —Å–≤–æ–π –ø—É—Ç—å */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -2;
}
/* –¢—ë–º–Ω–æ–µ –∑–∞—Ç–µ–º–Ω–µ–Ω–∏–µ –ø–æ–≤–µ—Ä—Ö –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è —Å –≤–∏–Ω—å–µ—Ç–∫–æ–π */
.background-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: radial-gradient(
    ellipse at center,
    rgba(0, 0, 0, 0) 40%,
    rgba(0, 0, 0, 0.5) 75%,
    rgba(0, 0, 0, 0.85) 100%
  );
  z-index: -1;
  pointer-events: none;
}

/* –û—Å–Ω–æ–≤–∞ –ø–∞–Ω–µ–ª–∏ */
.custom-nav {
  background-color: #2e2e2e !important;
  border-top: 1px solid #444;
  height: 55px;
}

/* –í—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã */
.custom-nav .MuiBottomNavigationAction-root {
  position: relative;
  color: #aaa !important;
  font-size: 12px;
  transition: color 0.3s ease, transform 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* –ê–∫—Ç–∏–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç */
.custom-nav .MuiBottomNavigationAction-root.Mui-selected {
  color: #ffa500 !important;
  transform: scale(1.07); /* —ç—Ñ—Ñ–µ–∫—Ç —É–≤–µ–ª–∏—á–µ–Ω–∏—è */
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –∑–Ω–∞—á–∫–∞ */
.custom-nav .MuiBottomNavigationAction-root .MuiSvgIcon-root {
  transition: transform 0.3s ease;
}

.custom-nav .MuiBottomNavigationAction-root.Mui-selected .MuiSvgIcon-root {
  transform: scale(1.15);
}

/* –ê–Ω–∏–º–∞—Ü–∏—è —Ç–µ–∫—Å—Ç–∞ */
.custom-nav .MuiBottomNavigationAction-label {
  transition: opacity 0.3s ease;
  opacity: 0.7;
}

.custom-nav
  .MuiBottomNavigationAction-root.Mui-selected
  .MuiBottomNavigationAction-label {
  font-weight: bold;
  opacity: 1;
}

/* –ü–æ–¥—á—ë—Ä–∫–∏–≤–∞–Ω–∏–µ ‚Äî —Ä–µ–≥—É–ª–∏—Ä—É–µ–º height –∑–¥–µ—Å—å */
.custom-nav .MuiBottomNavigationAction-root::after {
  content: "";
  position: absolute;
  bottom: 3px; /* –æ—Ç—Å—Ç—É–ø –æ—Ç –Ω–∏–∂–Ω–µ–≥–æ –∫—Ä–∞—è */
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: center;
  width: 50%;
  height: 4px; /* <‚Äî –∑–¥–µ—Å—å –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ—à—å —Ç–æ–ª—â–∏–Ω—É –ø–æ–ª–æ—Å–∫–∏ */
  background-color: #ffa500;
  border-radius: 2px;
  box-shadow: 0 0 6px rgba(255, 165, 0, 0.4);
  transition: transform 0.4s ease, opacity 0.4s ease;
  opacity: 0;
}

/* –í–∫–ª—é—á–∞–µ–º –ø–æ–ª–æ—Å—É —É –∞–∫—Ç–∏–≤–Ω–æ–π –≤–∫–ª–∞–¥–∫–∏ */
.custom-nav .MuiBottomNavigationAction-root.Mui-selected::after {
  transform: translateX(-50%) scaleX(1);
  opacity: 1;
}

/* === –£–¥–∞–ª—è–µ—Ç —Å–∏–Ω–∏–µ –≤—ã–¥–µ–ª–µ–Ω–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ/—Ç–∞–ø–µ –ø–æ —ç–ª–µ–º–µ–Ω—Ç–∞–º === */
*:focus {
  outline: none !important;
  box-shadow: none !important;
}

*:active {
  outline: none !important;
  box-shadow: none !important;
}

* {
  -webkit-tap-highlight-color: transparent;
  -webkit-focus-ring-color: transparent;
}
