.raid-page {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.boss-name {
  font-size: 4vw;
  margin-bottom: 1vh;
}
.boss-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #000;
}

.damage-multiplier-effects {
  margin-top: 4px;
  color: #ffa500;
  font-weight: bold;
  font-size: 14px;
  text-shadow: 0 0 3px #000;
}

.damage-multiplier-effect {
  margin-right: 6px;
  display: inline-block;
}

.boss-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.7);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.boss-info-overlay {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  color: #fff;
  text-align: center;
}

.boss-hp-bar {
  position: relative;
  width: 80vw;
  max-width: 600px;
  height: 20px;
  background: #555;
  border-radius: 10px;
  margin-top: 8px;
  overflow: hidden;
}
.hp-bar-fill {
  height: 100%;
  background: linear-gradient(90deg, #ff5555, #ff0000);
  border-radius: 10px 0 0 10px;
  position: relative;
  z-index: 2;
  transition: width 0.6s ease;
}

.hp-label {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
  font-weight: bold;
  color: white;
  text-shadow: 0 0 5px #000;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  z-index: 4;
}
.hp-bar-damage {
  position: absolute;
  top: 0;
  right: 0; /* –ë–µ–ª—ã–π —Ñ—Ä–∞–≥–º–µ–Ω—Ç –ø—Ä–∏–∫—Ä–µ–ø–ª–µ–Ω –∫ –ø—Ä–∞–≤–æ–º—É –∫—Ä–∞—é –∫—Ä–∞—Å–Ω–æ–π –ø–æ–ª–æ—Å—ã */
  height: 100%;
  background-color: white;
  border-radius: 0 10px 10px 0;
  z-index: 3;
  transition: width 1.2s ease;
  pointer-events: none;
}

.player-hand-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 12px 48px 24px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  z-index: 3;
}

.boss-hp-bar {
  position: relative;
  width: 300px;
  height: 24px;
  background-color: #555; /* —Ñ–æ–Ω –ø–æ–ª–æ—Å—ã (—Å–µ—Ä—ã–π) */
  border-radius: 12px;
  overflow: hidden;
  margin-top: 10px;
}

.hp-bar-fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: #ff4444; /* –∫—Ä–∞—Å–Ω–æ–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ */
  border-radius: 12px 0 0 12px;
  transition: width 0.5s ease;
  z-index: 1;
  width: 0;
}

.hp-bar-damage {
  position: absolute;
  top: 0;
  bottom: 0;
  background-color: white;
  opacity: 0.6;
  border-radius: 0; /* –±–µ–∑ —Å–∫—Ä—É–≥–ª–µ–Ω–∏–π */
  transition: width 1.2s ease, opacity 0.3s ease;
  z-index: 2;
  pointer-events: none;
}
.boss-info-overlay {
  position: relative;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  color: #fff;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 2.2rem;
  letter-spacing: 1.5px;
  padding: 8px 24px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  user-select: none;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.9);
  min-width: 220px;
  max-width: 90vw;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.boss-hp-bar {
  position: relative;
  width: 80vw;
  max-width: 600px;
  height: 28px;
  margin: 12px auto 0;
  background: linear-gradient(135deg, #333333, #555555);
  border-radius: 14px;
  box-shadow: inset 0 2px 6px rgba(255, 0, 0, 0.4),
    0 0 12px rgba(255, 0, 0, 0.6);
  overflow: hidden;
  user-select: none;
  z-index: 10;
}

.hp-bar-fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(90deg, #ff5555, #ff0000);
  border-radius: 14px 0 0 14px;
  box-shadow: 0 0 8px 2px rgba(255, 50, 50, 0.8),
    inset 0 0 8px 2px rgba(255, 150, 150, 0.7);
  transition: width 0.6s ease;
  z-index: 2;
  will-change: width;
}

.hp-bar-fill.pulse {
  animation: pulseGlow 1.2s infinite alternate ease-in-out;
}

@keyframes pulseGlow {
  from {
    box-shadow: 0 0 8px 3px rgba(255, 80, 80, 0.9),
      inset 0 0 10px 3px rgba(255, 180, 180, 0.9);
  }
  to {
    box-shadow: 0 0 12px 6px rgba(255, 80, 80, 1),
      inset 0 0 15px 6px rgba(255, 180, 180, 1);
  }
}

.hp-label {
  position: relative;
  z-index: 5;
  font-weight: 700;
  color: white;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.85), 0 0 10px rgba(0, 0, 0, 0.7);
  font-size: 1.1rem;
  line-height: 28px;
  user-select: none;
  pointer-events: none;
  text-align: center;
  white-space: nowrap;
}

.hp-label {
  position: relative;
  z-index: 3;
  text-align: center;
  font-weight: bold;
  color: white;
  user-select: none;
  margin-top: 4px;
}

@keyframes screenShake {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(-5px, 5px);
  }
  40% {
    transform: translate(5px, -5px);
  }
  60% {
    transform: translate(-5px, 5px);
  }
  80% {
    transform: translate(5px, -5px);
  }
  100% {
    transform: translate(0, 0);
  }
}

.raid-page.shake {
  animation: screenShake 0.4s ease;
}
/* –ê–Ω–∏–º–∞—Ü–∏—è –∫—Ä–∞—Å–Ω–æ–π –≤—Å–ø—ã—à–∫–∏ */
@keyframes flashRed {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
  }
}

/* –û–±—â–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ –¥–ª—è –≤—Å–ø—ã—à–µ–∫ */
.red-flash {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10vw;
  max-width: 120px;
  background: linear-gradient(to right, rgba(255, 0, 0, 0.25), transparent);
  opacity: 0;
  pointer-events: none;
  z-index: 9999;
}

/* –õ–µ–≤–∞—è —Å—Ç–æ—Ä–æ–Ω–∞ */
.red-flash.left {
  left: 0;
}

/* –ü—Ä–∞–≤–∞—è —Å—Ç–æ—Ä–æ–Ω–∞ (–æ—Ç–∑–µ—Ä–∫–∞–ª–∏–≤–∞–Ω–∏–µ —á–µ—Ä–µ–∑ rotateY) */
.red-flash.right {
  right: 0;
  transform: rotateY(180deg);
}

/* –ö–ª–∞—Å—Å –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ */
.red-flash.animate {
  animation: flashRed 0.4s ease-in-out;
}

.damage-number {
  position: absolute;
  top: 40px; /* —á—É—Ç—å –≤—ã—à–µ HP –±–∞—Ä–∞, –ø–æ–¥–∫–æ—Ä—Ä–µ–∫—Ç–∏—Ä—É–π—Ç–µ –ø–æ –Ω—É–∂–¥–∞–º */
  left: 80%;
  transform: translateX(-50%);
  color: #ff4d4d;
  font-weight: bold;
  font-size: 1.5em;
  pointer-events: none;
  user-select: none;
  animation: damage-fly-up 1s ease forwards;
  text-shadow: 0 0 5px rgba(255, 0, 0, 0.8);
}

@keyframes damage-fly-up {
  0% {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-50%) translateY(+50px);
  }
}
.energy-warning {
  position: fixed;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 80, 80, 0.9);
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  font-size: 1rem;
  z-index: 1000;
  animation: fadeOut 2s forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
