.game-container {
  position: relative;
  height: 100vh;
  background: linear-gradient(to top, #1a1a1a, #2e2e2e);
  color: white;
  font-family: sans-serif;
  box-sizing: border-box;
  overflow: hidden;
}

/* –õ–µ–≤—ã–π –≤–µ—Ä—Ö/–Ω–∏–∑ ‚Äî –æ–±—â–∞—è –ª–∏–Ω–∏—è –Ω–∏–∫ + HP */
.info-row {
  position: absolute;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 10px 16px;
  box-sizing: border-box;
}

.info-row.top {
  top: 0;
  justify-content: flex-start;
}

.info-row.bottom {
  bottom: 0;
  justify-content: flex-start;
}

.nickname {
  font-size: 16px;
  white-space: nowrap;
  color: #fff;
}

/* HP –∞–¥–∞–ø—Ç–∏–≤–Ω—ã–π */
.hp-bar {
  flex: 1;
  max-width: 60%;
  height: 20px;
  background-color: #444;
  border: 1px solid #777;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.hp-fill {
  height: 100%;
  background-color: #e53935;
  transition: width 0.4s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 12px;
}

.hp-text {
  z-index: 1;
}

/* –ö–∞—Ä—Ç—ã –∏–≥—Ä–æ–∫–∞ */
.hand-section {
  position: absolute;
  bottom: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 10px;
  overflow: visible; /* –≤–∞–∂–Ω–æ, —á—Ç–æ–±—ã –∫–Ω–æ–ø–∫–∞ –Ω–µ –æ–±—Ä–µ–∑–∞–ª–∞—Å—å */
}

.game-card {
  position: relative; /* –¥–ª—è –∞–±—Å–æ–ª—é—Ç–Ω–æ–≥–æ –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–æ–ø–∫–∏ */
  background-color: #333;
  border: 1px solid #fff;
  padding: 8px;
  border-radius: 8px;
  width: 90px;
  text-align: center;
  cursor: pointer;
}

.game-card-img {
  width: 80px;
  height: auto;
  margin-bottom: 5px;
}

.game-card.selected .play-card-button {
  display: block;
}

.info-container {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none; /* –ß—Ç–æ–±—ã –Ω–µ –º–µ—à–∞–ª–æ –∫–ª–∏–∫–∞–º */
}

.info-container.top {
  top: 20px;
}

.info-container.bottom {
  bottom: 20px;
}

.hp-bar-container {
  position: absolute;
  left: 20px;
  width: 50%;
  max-width: 250px;
}

.hp-bar {
  background-color: #444;
  height: 18px;
  border-radius: 9px;
  overflow: hidden;
  position: relative;
}

.hp-fill {
  background-color: #ff3e3e;
  height: 100%;
  transition: width 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 12px;
}

.nickname.center {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  position: relative;
  text-align: center;
}

.hp-text {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 13px;
}

.play-card-button {
  position: absolute;
  top: -40px; /* —á—É—Ç—å –≤—ã—à–µ –∫–∞—Ä—Ç—ã */
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  padding: 6px 12px;
  background-color: #ffa500;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: calc(100% + 20px); /* —á—É—Ç—å —à–∏—Ä–µ —Å–∞–º–æ–π –∫–∞—Ä—Ç—ã */
  box-sizing: border-box;
  text-align: center;
  display: none; /* –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é —Å–∫—Ä—ã—Ç–∞ */
}

/* –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –∫–Ω–æ–ø–∫—É —Ç–æ–ª—å–∫–æ —É –≤—ã–±—Ä–∞–Ω–Ω–æ–π –∫–∞—Ä—Ç—ã */
.card.selected .play-card-button {
  display: block;
}
.recipes-container {
  position: fixed; /* —Ñ–∏–∫—Å–∏—Ä—É–µ–º –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ —ç–∫—Ä–∞–Ω–∞ */
  right: 20px;
  bottom: 20px;
  background-color: #ffa500;
  color: #1a1a1a;
  font-weight: bold;
  font-size: 16px;
  padding: 8px 12px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 8px rgba(255, 165, 0, 0.6);
  user-select: none;
  cursor: default;
  z-index: 1000; /* —á—Ç–æ–±—ã –±—ã—Ç—å –ø–æ–≤–µ—Ä—Ö */
}
.end-turn-button {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 10;
  padding: 8px 16px;
  background-color: #ffa500;
  color: black;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 0 10px #00000060;
  transition: background-color 0.2s;
}

.end-turn-button:hover {
  background-color: #ff8c00;
}

.played-cards-section {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  gap: 16px;
}

.played-card {
  width: 120px;
  height: 180px;
  border: 2px solid #ffa500;
  border-radius: 8px;
  background-color: #2e2e2e;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  color: white;
  box-shadow: 0 0 10px #ffa500;
}
.undo-card-button {
  margin-top: 6px;
  padding: 4px 10px;
  font-size: 12px;
  cursor: pointer;
  background-color: #ff4d4d;
  border: none;
  border-radius: 4px;
  color: white;
  transition: background-color 0.2s ease;
}

.undo-card-button:hover {
  background-color: #cc0000;
}

.played-card-img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 6px;
}
